button {
	&:hover {
		cursor: pointer;
	}
}

.btn {
	@include transition;
	border-radius: 0;
	padding: rem-calc(16) rem-calc(24) rem-calc(14);
	text-transform: uppercase;
	line-height: 1.71;
	font-family: fonts(mpb);
	font-size: rem-calc(14);
	box-sizing: border-box;
	display: inline-block;
	margin: rem-calc(5) 0;
	letter-spacing: .8px;

	&:hover {
		cursor: pointer;
	}

	&:active,
	&:focus {
		outline: none;
	}

	@at-root #{&}--rounded {
		border-radius: 3px;
	}
	@at-root #{&}--blue {
		border: 2px solid color-preset(ecta-blue);
		background: color-preset(ecta-blue);
		color: color-preset(white);

		&:active,
		&:focus,
		&:hover {
			background: color-preset(white);
			color: color-preset(ecta-blue);
		}
	}

	@at-root #{&}--white {
		border: 2px solid rgba(color-preset(black), 0.1);
		background-color: rgba(color-preset(white), 0);
		color: color-preset(ecta-blue);

		&:active,
		&:focus,
		&:hover {
			border: 2px solid color-preset(ecta-blue);
			background: rgba(color-preset(white), 1);
			color: color-preset(ecta-blue);
		}
	}

	@at-root #{&}--simple {
		background: color-preset(white);
		color: color-preset(ecta-blue);
		letter-spacing: 1.1px;
		line-height: 1em;
		padding: rem-calc(20) rem-calc(24) rem-calc(18);
		border: none;

		&:active,
		&:focus,
		&:hover {
			background: color-preset(ecta-blue-10);
			color: color-preset(ecta-blue);
			border: none;
		}
	}

	@at-root #{&}--arrow {
		border: 1px solid color-preset(light-grey);
		background-color: color-preset(white);
		box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.06);
		color: color-preset(ecta-blue);
		text-transform: none;
		position: relative;
		font-family: fonts(mpr);
		font-size: rem-calc(18);
		padding: rem-calc(20) rem-calc(34) rem-calc(18) rem-calc(24);
		display: flex;

		&::after {
			position: absolute;
			right: 10px;
			height: 15px;
			width: 15px;
			background-image: url("../../img/icons/icon-caretright-lightgreen.svg");
			background-repeat: no-repeat;
			background-size: contain;
			content: '';
		}

		&:active,
		&:focus,
		&:hover {
			border: 1px solid color-preset(light-grey);
			background: color-preset(lighter-grey);
			color: color-preset(ecta-blue);
		}
	}

	@at-root #{&}--transparent {
		border: 1px solid color-preset(lighter-grey);
		color: color-preset(lighter-grey);

		&:active,
		&:focus,
		&:hover {
			background: rgba(color-preset(lighter-grey), 1);
			border: 1px solid color-preset(lighter-grey);
			color: color-preset(ecta-blue);
		}
	}

	@at-root #{&}--orange {
		border: 2px solid color-preset(ecta-orange);
		color: color-preset(white);
		background: rgba(color-preset(ecta-orange), 1);
		line-height: 1.33;
		letter-spacing: .9px;

		&:active,
		&:focus,
		&:hover {
			background: rgba(color-preset(ecta-orange), 0);
			border: 2px solid color-preset(ecta-orange);
			color: color-preset(ecta-orange);
		}
	}

	@at-root #{&}--large {
		font-size: rem-calc(18);
		width: 100%;
		padding: rem-calc(16) rem-calc(56) rem-calc(14);
	}

	@at-root #{&}--hamburger {
		@include transition;
		display: block;
		border: 0;
		background: transparent;
		padding: 0;
		width: 20px;
		height: 16px;
		line-height: 16px;

		&:active,
		&:focus,
		&:hover {
			background: transparent;

			.icon--hamburger {
				@include transition;

				&::before {
					width: 18px;
				}

				&::after {
					width: 14px;
				}
			}
		}
	}

	@at-root #{&}--search {
		@include transition;
		display: flex;
		border: 0;
		border-bottom: 2px solid rgba(color-preset(ecta-blue), 0);
		background: transparent;
		padding: 0;
		width: 20px;
		height: 20px;
		line-height: 16px;
		font-size: rem-calc(16);

		&:active,
		&:focus,
		&:hover {
			border-color: color-preset(ecta-blue);
			background: transparent;
			color: color-preset(ecta-blue);
		}
	}

	@at-root #{&}--filter {
		border-color: color-preset(ecta-blue);
		background-color: color-preset(ecta-blue);
		color: color-preset(white);

		&:active,
		&:focus,
		&:hover {
			border-color: color-preset(ecta-blue);
			background-color: color-preset(white);
			color: color-preset(ecta-blue);
		}
	}

	@at-root #{&}--viewall {
		display: block;
		margin: 28px 0;
		border: 2px solid color-preset(black);
		padding: 10px;
		text-align: center;
		font-weight: fw(normal);
	}

	@at-root #{&}--repeater {
		display: block;
		margin: rem-calc(10) 0;
		padding: rem-calc(16) rem-calc(10);

		@include breakpoint(bear(teen)) {
			width: calc(1 / 2 * 100% - (1 - 1 / 2) * 10px);
		}
	}

	@at-root #{&}--external, &[target='_blank'] {
		position: relative;
		padding-right: rem-calc(40);

		&::after {
			position: absolute;
			top: 20px;
			right: rem-calc(24);
			background-image: url("../../img/icons/icon-external-link-white.svg");
			background-repeat: no-repeat;
			background-size: contain;
			width: 12px;
			height: 12px;
			content: '';
		}

		&:active,
		&:focus,
		&:hover {
			&::after {
				background-image: url("../../img/icons/icon-external-link-lightblue.svg");
			}
		}
	}
}

#primary-menu {
	.menu__cta {
		padding-top: 0;
		box-shadow: none;

		a {
			@include transition;
			font-family: fonts(mpb);
			box-sizing: border-box;
			padding: rem-calc(12) rem-calc(30) rem-calc(10) !important;
			border-radius: 0;
			border: 2px solid color-preset(ecta-blue);
			background: color-preset(ecta-blue);
			color: color-preset(white);

			&:after {
				display: none;
			}

			&:active,
			&:focus,
			&:hover {
				cursor: pointer;
				background: color-preset(white);
				color: color-preset(ecta-blue);
				outline: none;
				box-shadow: none;
			}
		}
	}
}

.subtitle__buttons {

	@include breakpoint(bear(teen)) {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}
}

.post-edit-link {
	display: block;
	margin-bottom: 1.25rem;
}
