.feed {
	position: relative;

	h2 {
		text-align: center;
	}

	@at-root #{&}--news {
		padding-bottom: rem-calc(36);

		@include breakpoint(bear(mama)) {
			padding-bottom: 0;
		}

		.btn {
			position: absolute;
			bottom: 0;
			@include center(true, false, true);

			@include breakpoint(bear(mama)) {
				top: 0;
				right: 0;
				transform: none;
				bottom: auto;
				left: auto;
			}
		}
	}
}

.cards {
	@at-root #{&}--post {
		display: grid;
		grid-template-columns: 1fr;
		grid-column-gap: 80px;
		justify-items: stretch;
		align-items: stretch;
		grid-template-rows: auto auto;
		grid-template-areas: 'card-hero' 'card-columns';
		margin: rem-calc(36) auto;

		@include breakpoint(bear(mama)) {
			grid-template-columns: 1fr 1fr;
			grid-template-rows: auto;
			grid-template-areas: 'card-hero card-columns';

			@include ie11 {
				display: -ms-grid;
				-ms-grid-columns: 1fr 1fr;
				-ms-grid-rows: auto;
			}

		}

		a {
			border-bottom: 0;
		}

	}
}

.card {

	@at-root #{&}__img {

		margin-bottom: rem-calc(24);
	}

	@at-root #{&}--hero {

		grid-area: card-hero;

		@include ie11 {
			-ms-grid-row: 1;
			-ms-grid-column: 1;
		}

		.card__title {
			font-size: rem-calc(36);
		}
	}

	@at-root #{&}--featured-hero {
		.card__title {
			font-size: rem-calc(36);
		}

		display: grid;

		grid-template-columns: 1fr;
		grid-template-rows: auto auto auto auto;
		grid-template-areas: 'f_image' 'f_overline' 'f_title' 'f_meta';
		grid-row-gap: 20px;

		@include ie11 {
			display: -ms-grid;
			-ms-grid-columns: 1fr;
			-ms-grid-rows: auto auto auto auto;
		}

		&.hentry {
			margin-bottom: 0;
		}

		.card__excerpt,
		.card__img {
			margin-bottom: 0;
		}

		@include breakpoint(bear(mama)) {

			grid-template-columns: 800px 1fr;
			grid-template-rows: auto auto 1fr;
			grid-template-areas: 'f_image f_overline' 'f_image f_title' 'f_image f_meta';
			grid-column-gap: 10px;
			grid-row-gap: 0;

			@include ie11 {
				-ms-grid-columns: 800px 1fr;
				-ms-grid-rows: auto auto 1fr;
			}
		}
	}

	@at-root #{&}__columns {

		grid-area: card-columns;

		@include ie11 {
			margin-left: 80px;
			-ms-grid-row: 1;
			-ms-grid-column: 2;
		}

	}

	@at-root #{&}--columns {

		display: grid;
		grid-template-columns: 100px 1fr;
		grid-template-rows: auto auto 1fr;
		grid-template-areas: 'f_image f_overline' 'f_image f_title' 'f_image f_meta';
		grid-column-gap: 20px;
		grid-row-gap: 0;

		@include ie11 {
			display: -ms-grid;
			-ms-grid-columns: 100px 1fr;
			-ms-grid-rows: auto auto 1fr;
		}

		@include breakpoint(bear(mama)) {

			grid-template-columns: 142px 1fr;

			@include ie11 {
				-ms-grid-columns: 142px 1fr;
			}
		}
	}

	@at-root #{&}--teaser {

		display: grid;
		grid-template-columns: 1fr;
		grid-template-rows: auto auto auto auto;
		grid-template-areas: 'f_image' 'f_title' 'f_excerpt' 'f_meta';
		grid-row-gap: 20px;

		@include ie11 {
			display: -ms-grid;
			-ms-grid-columns: 1fr;
			-ms-grid-rows: auto auto auto auto;
		}

		@include breakpoint(bear(mama)) {

			grid-template-columns: 180px 1fr;
			grid-template-rows: auto auto 1fr;
			grid-template-areas: 'f_image f_title' 'f_image f_excerpt' 'f_image f_excerpt' 'f_image f_meta';
			grid-column-gap: 20px;
			grid-row-gap: 0;

			@include ie11 {
				-ms-grid-columns: 180px 1fr;
				-ms-grid-rows: auto auto 1fr;
			}
		}

		.card__excerpt {
			grid-area: f_excerpt;
			margin-bottom: 0;
		}
	}

	@at-root #{&}__img {
		position: relative;
		grid-area: f_image;

		@include ie11 {
			-ms-grid-row: 1;
			-ms-grid-row-span: 3;
			-ms-grid-column: 1;
			margin-right: 20px;
		}

		&-flag {
			position: absolute;
			top: 0;
			left: 0;
			background-color: color-preset(ecta-blue);
			padding: 6px 9px;
			letter-spacing: 1px;
			font-size: rem-calc(12);

			a {
				color: color-preset(white);

				&:active,
				&:focus,
				&:hover {
					color: color-preset(white);
				}
			}
		}

		&-hero-featured-flag {
			a {
				color: color-preset(white);
				background-color: color-preset(ecta-blue);
				padding: 6px 9px;
				letter-spacing: 1px;
				font-size: rem-calc(12);
				display: inline-block;
				margin-right: auto;

				&:active,
				&:focus,
				&:hover {
					color: color-preset(white);
				}
			}
		}
	}

	@at-root #{&}__overline {

		grid-area: f_overline;

		@include ie11 {
			-ms-grid-row: 1;
			-ms-grid-column: 2;
		}
	}

	@at-root #{&}__title {

		grid-area: f_title;
		@include ie11 {
			-ms-grid-row: 2;
			-ms-grid-column: 2;
		}
	}

	@at-root #{&}__meta {

		grid-area: f_meta;
		@include ie11 {
			-ms-grid-row: 3;
			-ms-grid-column: 2;
		}

		span {
			font-family: fonts(mpb);
		}
	}
}
