a {
	@include transition;
	text-decoration: none;
	color: $color__link;

	&:focus,
	&:hover {
		text-decoration: none;
		text-decoration-color: $color__link;
		color: color-preset(ecta-blue);
	}
}

.nav-pills {
	.nav-item {
		.btn {

			border-radius: 0;
			padding: 1rem 1.5rem .875rem;
			background-color: color-preset(white);
			color: color-preset(ecta-blue);
			box-shadow: none;
			border: 2px solid rgba(color-preset(black), 0.1);
			outline: none;
			outline-offset: none;

		}

		&.active {
			.btn {
				background-color: color-preset(ecta-blue);
				color: color-preset(white);
				// border: 2px solid color-preset(ecta-blue);
				border: 10px solid green;
			}
		}

	}
}
