.intro {

  @at-root #{&}--col-stacked {
    .overline {
      text-align: center;
    }

    .intro__title {
      text-align: center;
      max-width: 1037px;
      margin: 0 auto 20px;
    }
  }

}

.cards {

  @at-root #{&}--col-stacked {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: stretch;

    @include breakpoint(bear(papa)) {
      margin: 0 -15px;

    }
    @include breakpoint(bear(grandpa)) {
      flex-wrap: nowrap;
      width: auto;
    }
  }
}

.card {

  @at-root #{&}--col-stacked {
    display: block;
    margin: 25px 0;

    @include breakpoint(bear(papa)) {
      margin: 25px 15px;
      width: calc(100% / 2 - 30px);
    }

    @include breakpoint(bear(grandpa)) {}

    .btn {
      align-self: flex-end;
    }

    h3 {
      margin: rem-calc(24) 0;
    }

    p {
      font-size: 16px;
    }
  }
}
